export default {
	props:{
		page:{
			type:Object,
			default: {
				pageNum: 0,
				examNum: 10,
				examTime: null,
			}
		},
		allData:{
			type:Object,
			default: {}
		},
		problemList:{
			type:Array,
			default: []
		}
	},
	data() {
		return {
			topList:[],
			bottomList:[],
			countProblemList: this.problemList,
			currentTab:-1,
		};
	},
	watch: {
		page: {
			handler(val, oval) {
				this.currentTab = -1
				this.topList = this.allData.startGroupVoList[val.pageNum].startProblemVoList
				this.bottomList = this.allData.startGroupVoList[val.pageNum].startAnsweroList
			},
			immediate: true,
			deep: true
		},
	},
	mounted(){
		this.topList = this.allData.startGroupVoList[this.page.pageNum].startProblemVoList
		this.bottomList = this.allData.startGroupVoList[this.page.pageNum].startAnsweroList
	},
	methods:{
		chooseOne(item, index){
			this.currentTab = index
			const newProblem = {
				answerId: item.id,
				problemId: this.topList[0].id,
				problemName: this.topList[0].problemName,
				problemPic: this.topList[0].problemPic
			}
			this.countProblemList.push(newProblem)
			this.$emit('clickCountBottom', this.countProblemList)
		},
	}
}
